.menu-bar {
  background: -webkit-linear-gradient(top, #ffffff 0%, #e4e4e4 100%);
  background: linear-gradient(to bottom, #ffffff 0%, #e4e4e4 100%);
}
.menu {
  width: 90%;
  max-width: 991px;
  margin: 0 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.menu :global a.active {
  background: -webkit-linear-gradient(top, #a9a9a9 0%, #929292 100%);
  background: linear-gradient(to bottom, #a9a9a9 0%, #929292 100%);
  color: white;
}
.menu :global a.active:visited {
  color: white;
}
.menu a {
  padding: 10px 20px;
  border-right: 2px solid white;
  text-decoration: none;
  color: #555;
  font-size: 14px;
  font-weight: bold;
}
.menu a:nth-last-child(1) {
  border-right: none;
}
.menu a:visited {
  color: #555;
}



