.amount-unit {
  vertical-align: top;
  display: inline-block;
}
.amount,
.unit {
  vertical-align: baseline;
}
.material,
.fuzzy {
  width: 230px !important;
  display: inline-block;
}
.material {
  border: 1px solid #eee;
  border-radius: 5px;
  padding: 12px 8px;
}
.fuzzy,
.material,
.amount {
  margin: 5px 15px 5px 0;
}
.fuzzy > div {
  padding: 0 !important;
  box-shadow: none !important;
}



