:global * {
  box-sizing: border-box;
  color: #555;
}
:global body,
:global html {
  margin: 0;
  padding: 0;
}
:global h1,
:global h2,
:global h3,
:global a,
:global body,
:global input {
  font-family: helvetica, arial, sans-serif;
}
:global button,
:global input[type="submit"] {
  margin: 0 15px 15px 0;
  padding: 15px 20px;
  border: 1px solid rgba(0, 0, 0, 0.13);
  border-radius: 5px;
  background: white;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
  cursor: pointer;
}
:global button:hover,
:global input[type="submit"]:hover {
  background: rgba(0, 0, 0, 0.04);
}
:global input {
  margin: 0 15px 15px 0;
  vertical-align: top;
  width: 230px;
  padding: 8px 10px;
  border: 1px solid #eee;
  border-radius: 2px;
  line-height: 24px;
  outline: none;
  font-size: 16px;
  color: #666;
  box-sizing: border-box;
  font-family: inherit;
}
:global #main-content {
  width: 90%;
  max-width: 991px;
  margin: 0 auto;
}



